"use strict";

import './style.less';

const Page = require('modules/page/index');
const Input = require('modules/input/index');
const DialogWidget = require('modules/dialog/widget');
const Validator = require('modules/validator/index');

// 
class Qrcode extends React.Component {
	render(){
		let me = this;
		let {props} = me;
		return (
			<div className="qrcode">
				<div className="qrcode-layout">
					{/*  */}
					<div className="qrcode-add">
						<Input type="button" value=" " onClick={me.addQrcode.bind(me)} />
					</div>
					{/*  */}
					<table className="qrcode-table" width="100%">
						<thead>
							<tr>
								<td width="80" className="fn-TAC"></td>
								<td width="100" ></td>
								<td></td>
								<td width="50" className="fn-TAC"></td>
							</tr>
						</thead>
						<tbody>
							<tr>
								<td>
									<div className="qrcode-img"></div>
								</td>
								<td></td>
								<td>
									<br />
								</td>
								<td className="fn-TAC">
									<a href="javascript:;"></a><br />
									<a href="javascript:;"></a><br />
									<a href="javascript:;"></a>
								</td>
							</tr>
						</tbody>
					</table>
					{/*  */}
					<Page className="qrcode-page fn-MT10" />
				</div>
			</div>
		);
	}
	addQrcode(){
		let me = this;
		let {props} = me;
		let {originData} = props;
		new DialogWidget({height:'auto', width: 'auto'}, null, 
			<div className="qrcode-dialog">
				<Validator executeSuccess={function(){console.log('success')}}>
					<table>
						<tbody>
							<tr>
								<td className="fn-VAM"></td>
								<td>
									<Input type="text" 
										rule="required"
										errMessage=""
										name="name" 
										value={originData.name} 
										placeholder="" />
								</td>
							</tr>
							<tr>
								<td className="fn-VAM"></td>
								<td><Input type="text" name="url" value="" placeholder="" /></td>
							</tr>
							<tr>
								<td className="qrcode-dialog-info"></td>
								<td>
									<Input type="textarea" name="info" value="" placeholder="" />
								</td>
							</tr>
							<tr>
								<td> </td>
								<td>
									<Input type="submit" value=" " />
									<Input type="reset" value=" " className="fn-ML10" />
								</td>
							</tr>
						</tbody>
					</table>
				</Validator>
			</div>
		);
	}
};

module.exports = Qrcode;




